import React, { Component } from "react";
import homeHero from "../../images/hero-images/home.jpg";
import home1 from "../../images/window-images/home-1.jpg";
import home2 from "../../images/window-images/home-2.jpg";
import hsqHero from "../../images/hero-images/hsq.jpg";
import hsq1 from "../../images/window-images/hsq-1.jpg";
import hsq2 from "../../images/window-images/hsq-2.jpg";
import hsq3 from "../../images/window-images/hsq-3.jpg";
import servicesHero from "../../images/hero-images/services.jpg";
import services1 from "../../images/window-images/services-1.jpg";
import services2 from "../../images/window-images/services-2.jpg";
import services3 from "../../images/window-images/services-3.jpg";
import services4 from "../../images/window-images/services-4.jpg";
import customerResourcesHero from "../../images/hero-images/customer-resources.jpg";
import customerResources1 from "../../images/window-images/customer-resources-1.jpg";
import contactUsHero from "../../images/hero-images/contact-us.jpg";
import driverResourcesHero from "../../images/hero-images/driver-resources.jpg";
import driverResources1 from "../../images/window-images/driver-resources-1.jpg";
import building from "../../images/window-images/building.jpg";
import divider from "../../images/mobile-divider.png";


class MobilePage extends Component {

  getImage() {
    switch (this.props.image) {
      case "home-hero":
        return homeHero;
      case "home-1":
        return home1;
      case "home-2":
        return home2;
      case "hsq-hero":
        return hsqHero;
      case "hsq-1":
        return hsq1;
      case "hsq-2":
        return hsq2;
      case "hsq-3":
        return hsq3;
      case "services-hero":
        return servicesHero;
      case "services-1":
        return services1;
      case "services-2":
        return services2;
      case "services-3":
        return services3;
      case "services-4":
        return services4;
      case "customer-resources-hero":
        return customerResourcesHero;
      case "customer-resources-1":
        return customerResources1;
      case "contact-us-hero":
        return contactUsHero;
      case "driver-resources-hero":
        return driverResourcesHero;
      case "driver-resources-1":
        return driverResources1;
      case "building":
        return building;
      default:
        return home1;
    }
  }

  getButton() {
    if (this.props.showButton === true) {
      return (
        <div className="hero-btn-container">
          <div className="hero-btn">
            <a
              rel="noopener noreferrer"
              target="_blank"
              href="http://eagent.healthex.e-courier.com"
            >
              Driver Login
            </a>
          </div>
        </div>
      )
    }
  }

  getHeroTitle() {
    if (this.props.heroTitle !== undefined) {
      return (
        <div className={this.props.image === "hsq-hero" ? "mobile-title-container hsq" : "mobile-title-container"}>
          <h2>{this.props.heroTitle}</h2>
        </div>
      )
    }
  }

  render() {
    return (
      <div className="mobile-only">
        
        {this.props.hero === true ? <div className="mobile-hero-scrolled"></div> : <div></div>}
        {this.props.image === undefined ? 
          <div className="mobile-divider">
            <img src={divider} alt="-----" />
          </div> :
          <div
            className="image-container"
            style={{ backgroundImage: `url("${this.getImage()}")` }}
          >
            <div className="image-overlay">
              {this.getHeroTitle()}
              {this.getButton()}
            </div>
          </div>
        }
        
        <div className="page-container">
          {this.props.title !== undefined ? (
            <div className="page-title">
              <h2>{this.props.title}</h2>
            </div>
          ) : (
            <div className="no-title"></div>
          )}
          <div className="page-content">{this.props.content}</div>
        </div>
      </div>
    );
  }
}

export default MobilePage;
