import React from 'react'
import "../App.css";
import Hero from "../components/desktop/Hero";
import SmallPage from "../components/desktop/SmallPage";
import TextLeft from "../components/desktop/TextLeft";
import TextRight from "../components/desktop/TextRight";
import Divider from "../components/desktop/Divider";
import MobilePage from "../components/mobile/MobilePage";

export default function Hsq(props) {
    return (
        <div>
            {/* Mobile Components */}
            <MobilePage 
              hero={true}
              heroTitle={props.title}
              image="hsq-hero"
              title={props.sections[0].title}
              content={props.sections[0].content}
            />
            <MobilePage 
              image="hsq-1"
              title={props.sections[1].title}
              content={props.sections[1].content}
            />
            <MobilePage
              title={props.sections[2].title}
              content={props.sections[2].content}
            />
            <MobilePage 
              title={props.sections[3].title}
              content={props.sections[3].content}
            />
            <MobilePage 
              image="hsq-2"
              title={props.sections[4].title}
              content={props.sections[4].content}
            />
            <MobilePage 
              title={props.sections[5].title}
              content={props.sections[5].content}
            />
            <MobilePage
              title={props.sections[6].title}
              content={props.sections[6].content}
            />
            <MobilePage
              title={props.sections[7].title}
              content={props.sections[7].content}
            />
            <MobilePage
              image="hsq-3"
              content={props.sections[8].content}
            />
            <MobilePage
              content={props.sections[9].content}
            />
            <MobilePage
              title={props.sections[10].title}
              content={props.sections[10].content}
            />

            {/* Desktop Components */}

            <Hero
              title={props.title}
              subtitle={props.subtitle}
              image={props.switch}
            />
            <SmallPage
              title={props.sections[0].title}
              content={props.sections[0].content}
            />
            <TextLeft
              image="hsq-1"
              title={props.sections[1].title}
              content={props.sections[1].content}
            />
            <TextRight
              image="hsq-1"
              title={props.sections[2].title}
              content={props.sections[2].content}
            />
            <SmallPage
              title={props.sections[3].title}
              content={props.sections[3].content}
            />
            <TextLeft
              image="hsq-2"
              title={props.sections[4].title}
              content={props.sections[4].content}
            />
            <TextRight
              image="hsq-2"
              title={props.sections[5].title}
              content={props.sections[5].content}
            />
            <SmallPage
              title={props.sections[6].title}
              content={props.sections[6].content}
            />
            <Divider />
            <SmallPage
              title={props.sections[7].title}
              content={props.sections[7].content}
            />
            <TextLeft
              image="hsq-3"
              content={props.sections[8].content}
            />
            <TextRight
              image="hsq-3"
              content={props.sections[9].content}
            />
            <SmallPage
              title={props.sections[10].title}
              content={props.sections[10].content}
            />
        </div>
    )
}
