import React, { Component } from "react";
import image from "../../images/hero-images/customer-resources.jpg";
// import axios from "axios";

class MobileInsurance extends Component {
  state = {
    company: "",
    address: "",
    phone: "",
    email: "",
    comments: "",
  }

  // async submit(company, address, phone, email, comments) {
  //   await axios.post('./api/insurance-cert-form', {
  //     company,
  //     address,
  //     phone,
  //     email,
  //     comments
  //   });
  // }

  setBorderRed(input) {
    input.style.border = "1px solid red";
  }

  setBorderDefault(input) {
    input.style.border = "1px solid #888";
  }

  submitForm(e) {
    e.preventDefault();
    const { company, address, phone, email, comments } = this.state;

    const values = [
      company,
      address,
      phone,
      email,
      comments
    ]

    const inputs = [
      document.querySelector('#mobInsCompany'), 
      document.querySelector('#mobInsAddress'),
      document.querySelector('#mobInsPhone'),
      document.querySelector('#mobInsEmail'),
      document.querySelector('#mobInsComments')
    ];

    let popup;
    const successPopup = document.querySelector('.submit-success-popup');
    const errorPopup = document.querySelector('.submit-error-popup');
    if (company !== "" && address !== "" && phone !== "" && email !== "" && comments !== "") {
      inputs.forEach((input) => {
        this.setBorderDefault(input);
      })
      errorPopup.style.opacity = 0;
      successPopup.style.opacity = 1;
      popup = successPopup;
      // this.submit(company, address, phone, email, comments);
      this.setState({ 
        company: "",
        address: "",
        phone: "",
        email: "",
        comments: ""
      });
    } else {
      for (let i = 0; i < values.length; i++) {
        if (values[i] === "") {
          this.setBorderRed(inputs[i]);
        } else {
          this.setBorderDefault(inputs[i]);
        }
      }
      successPopup.style.opacity = 0;
      errorPopup.style.opacity = 1;
      popup = errorPopup;
    }
    // popup.classList.add('popup-visible');
    setTimeout(function() {
      popup.style.opacity = 0;
    }, 2000);
  }

  handleChange = e => {
    this.setState({[e.target.name]: e.target.value});
  }

  render() {
    return (
    <div
      className="mobile-only"
    >
        <div
            className="image-container"
            style={{ backgroundImage: `url("${image}")` }}
          >
            <div className="image-overlay"></div>
          </div>
        <div className="page-container">
          <div className="page-title">
            <h2>Request Insurance Certificate</h2>
          </div>
          <div className="page-content">
            <div className="mobile-form-container">
              <p>
                If you require an updated insurance certificate, please fill out
                the following request:
              </p>
              <form>
                <input
                  name="company"
                  value={this.state.company}
                  onChange={this.handleChange}
                  id="mobInsCompany"
                  placeholder="Enter Company Name *"
                  required
                />
                <br />
                <input
                  name="address"
                  value={this.state.address}
                  onChange={this.handleChange}
                  id="mobInsAddress"
                  placeholder="Enter Street Address *"
                  required
                />
                <br />
                <input
                  name="phone"
                  value={this.state.phone}
                  onChange={this.handleChange}
                  id="mobInsPhone"
                  placeholder="Enter Phone Number *"
                  required
                />
                <br />
                <input
                  name="email"
                  value={this.state.email}
                  onChange={this.handleChange}
                  id="mobInsEmail"
                  placeholder="Enter Email Address *"
                  required
                />
                <br />
                <textarea
                  name="comments"
                  value={this.state.comments}
                  onChange={this.handleChange}
                  id="mobInsComments"
                  cols="30"
                  rows="10"
                  placeholder="Comments *"
                  required
                ></textarea>
                <div className="btn-container">
                  <button
                    className="default-btn dark-btn"
                    onClick={e => this.submitForm(e)}
                  >
                    Submit
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
  );
  }
  
}

export default MobileInsurance;